.table {
  border-right: 2px solid #f4f4f4;
  border-left: 2px solid #f4f4f4;
  width: 100%;
  .header {
    display: flex;
    background-color: #f5f7fa;
    flex-direction: row;
    flex-wrap: nowrap;
    font-size: 18px;    
  }
  .cell {
    flex-grow: 1;
    flex-shrink: 1;  
    flex-basis: 0;  
    padding: 10px;
    text-align: start;
  }
  .body {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    .row {
      display: flex;
      flex-direction: row;
      border-bottom: 2px solid #f4f4f4;
      padding-top: 5px;      
      .cell {
        font-size: 16px;
      }
    }
  }
}
